

































import {Component, Prop, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import {INotificationChannel, ISessionTicket , IStudentProfile, ITrainerProfile} from "@chaosinsight/yogipeople-portalclient"
import UserUpdate from "@/components/UserUpdate.vue"
import UserNotifications from "@/components/UserNotifications.vue"

@Component({ components: { UserNotifications, UserUpdate } })
export default class UserAccount extends Vue {
	@Prop()
	public profile!: IStudentProfile | ITrainerProfile

	@Prop()
	private isTrainer!: boolean

	private sessionTickets: ISessionTicket[] = []
	private notificationChannels: INotificationChannel[] = []

	private dataType: string | null = null

	private get isStudent(): boolean | null {
		return Portal.isStudent
	}

	private mounted(): void {
		this.notificationChannelConfigurations()
			.then(res => this.notificationChannels = res)
		if (this.isStudent)
			this.getSessionTickets()
				.then(res => this.sessionTickets = res)
	}

	/**
	 * Get session tickets
	 */

	private getSessionTickets(): Promise<ISessionTicket[]> {
		return Portal.client.call.sessionTickets.get()
			.response.then(res => res)
	}

	/**
	 * Push notifications channel configurations
	 */

	private notificationChannelConfigurations(): Promise<INotificationChannel[]> {
		return Portal.client.call.account.notificationChannelConfigurations()
			.response.then(res => res)
	}

	private close(): void {
		this.$emit("close")
	}
}
